import TypeIt from "typeit-react";



export default function SecondAnimation() {
    // const codeRef = useCallback(node => {
    //   type(node, 'Hello', 1000, 'Hello world!');
    // }, []);

    return(
      <div className="m-auto relative">

        <header >
          <p className="px-5 text-center text-gray-50 text-2xl md:text-6xl content-center ">
          { <TypeIt id="textAnimation" options={{
            loop: true,
            loopDelay: 10000,
            cursorSpeed: 1500
          }} getBeforeInit={(instance) => {
              instance
                .type("Hi, I'm Christine, a full stack")
                .pause(300)
                .delete(10)
                .pause(400)
                .type("fullstack developer! :)"


                )

              return instance;
          }}/> }
          </p>
        </header>


      </div>
    )
  }
